export default {
    data() {
        return {
          aftersales_bn:'',
          detail:{},
          goods:{}
        };
    },
    onLoad:function(options){
      this.aftersales_bn = options.aftersales_bn
    },
    onShow(){
      this.getDetail()
    },
    methods: {
      getDetail(){
        this.$http({
          aurl:'aftersalesDetail',
          params:{
            aftersales_bn:this.aftersales_bn
          },
          success:res => {
            if(res.data.code===0){
              console.log(res)
              this.detail = res.data.result.info.info
              this.goods = this.detail.trade
            }
          },
          fail:error => {
            console.log(error)
          }
        })
      }
    },
    mounted() {
    },
};
